.todo__tasks {
  margin-top: 30px; }
  .todo__tasks h2 {
    font-family: Montserrat;
    font-weight: bold;
    font-size: 22px;
    color: #c9d1d3;
    position: absolute;
    left: 50%;
    top: 65%; }
  .todo__tasks .done {
    opacity: 0.4; }
  .todo__tasks .low {
    border: 2px dotted green;
    box-shadow: 1px 2px 20px green; }
  .todo__tasks .medium {
    border: 2px dotted #ff6600;
    box-shadow: 1px 2px 20px #ff6600; }
  .todo__tasks .high {
    border: 2px dotted red;
    box-shadow: 1px 2px 20px red; }
  .todo__tasks .task {
    box-shadow: 1px 2px 20px #f3f3f3;
    border-radius: 12px;
    padding: 10px;
    flex-wrap: wrap;
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    cursor: pointer; }
    .todo__tasks .task:hover .delete_task {
      opacity: 1; }
    .todo__tasks .task-priority {
      display: flex; }
      .todo__tasks .task-priority_low {
        width: 15px;
        height: 15px;
        border-radius: 50%;
        background: #4dd599;
        margin-right: 3px; }
      .todo__tasks .task-priority_medium {
        width: 15px;
        height: 15px;
        border-radius: 50%;
        background: #ff6600;
        margin-right: 3px; }
      .todo__tasks .task-priority_high {
        width: 15px;
        height: 15px;
        border-radius: 50%;
        background: red;
        margin-right: 3px; }
    .todo__tasks .task-date {
      width: 40%;
      margin-left: 40px;
      opacity: 0.8; }
      .todo__tasks .task-date img {
        width: 13px;
        height: 13px;
        margin: 0; }
      .todo__tasks .task-date .date {
        font-size: 10px;
        margin-left: 7px;
        color: black; }
    .todo__tasks .task span {
      margin-left: 15px;
      border: 0;
      font-size: 16px;
      width: 80%; }
    .todo__tasks .task input {
      background: #ffffff;
      border: 1px solid #efefef;
      box-sizing: border-box;
      border-radius: 4px;
      padding: 8px 12px;
      font-size: 16px;
      margin-left: 15px;
      width: 80%; }
      .todo__tasks .task input:focus {
        border-color: #dbdbdb; }
    .todo__tasks .task .checkbox input {
      display: none; }
    .todo__tasks .task .checkbox svg {
      transition: opacity 0.15s ease-in-out; }
      .todo__tasks .task .checkbox svg path {
        stroke: #f2f2f2; }
    .todo__tasks .task .checkbox input:checked + label {
      background-color: #4dd599;
      border-color: #4dd599; }
      .todo__tasks .task .checkbox input:checked + label svg {
        opacity: 1; }
        .todo__tasks .task .checkbox input:checked + label svg path {
          stroke: #fff; }
    .todo__tasks .task .checkbox:hover label {
      background-color: #f2f2f2;
      border-color: #f2f2f2; }
      .todo__tasks .task .checkbox:hover label svg {
        opacity: 1; }
        .todo__tasks .task .checkbox:hover label svg path {
          stroke: #b2b2b2; }
    .todo__tasks .task .checkbox label {
      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px solid #e8e8e8;
      border-radius: 30px;
      width: 20px;
      height: 20px;
      cursor: pointer;
      transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out; }
      .todo__tasks .task .checkbox label svg {
        opacity: 0; }
    .todo__tasks .task .delete_task {
      display: flex;
      opacity: 0;
      transition: opacity 0.15s ease-in-out; }
      .todo__tasks .task .delete_task div {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
        background-color: #fafbfc;
        border-radius: 6px;
        margin-left: 5px;
        cursor: pointer; }
        .todo__tasks .task .delete_task div:hover {
          background-color: #eef1f4; }
          .todo__tasks .task .delete_task div:hover svg {
            opacity: 0.9; }
        .todo__tasks .task .delete_task div svg {
          width: 11px;
          height: 11px;
          opacity: 0.4; }
